import SearchBar from '@/components/SearchBar'
const PAGE_SIZE = 6
export default {
  components: {
    SearchBar
  },
  data () {
    return {
      menuList: ['综合', '销量', '价格'],
      acitveIndex: 0,
      query: '',
      goodsList: [],
      isLastPage: false,
      isFixed: false
    }
  },
  onLoad (options) {
    this.query = options.query
    this.reload()
  },
  onPullDownRefresh () {
    this.isFixed = false
    //  加载第一页
    this.reload()
  },
  onPageScroll () {
    this.isFixed = true
  },
  // 上拉加载下一页
  onReachBottom () {
    this.pageNum++
    this.queryGoodsList()
  },
  methods: {
		changeIndex(index){
			this.acitveIndex = index
		},
    search (data) {
      this.query = data
      this.reload()
    },
    // 去到商品详情
    toItem (id) {
      wx.navigateTo({ url: '/pages/item/main?id=' + id })
    },
    reload () {
      //  加载第一页
      this.pageNum = 1
      this.isLastPage = false
      this.isRequest = false
      this.goodsList = []
      this.queryGoodsList()
    },
    queryGoodsList () {
      // 如果请求中，就不再请求
      if (this.isRequest || this.isLastPage) {
        return
      }

      this.isRequest = true
      this.$request({
        url: '/api/public/v1/goods/search',
        data: {
          query: this.query,
          pagenum: this.pageNum,
          pagesize: PAGE_SIZE
        }
      }).then(data => {
        // console.log(data)
        this.goodsList = [...this.goodsList, ...data.goods]
        // 判断是否到了最后一页
        if (this.goodsList.length === data.total) {
          this.isLastPage = true
        }
      }).finally(() => {
        // 请求完成后，允许再请求了
        this.isRequest = false
      })
    }
  }
}
